import delay from "../functions/delay";
import createDiv from "../functions/createDiv";

const newPromise = () => (
  setLightRed,
  setLightYellow,
  setLightGreen,
  setStart,
  setResolve,
  setDone
) => {

    createDiv(
      "red",
      setLightRed,
      setLightYellow,
      setLightGreen,
      setStart,
      setResolve,
      setDone
    )
    .then(() => delay(3000))
    .then(() => createDiv(
      "yellow",
      setLightRed,
      setLightYellow,
      setLightGreen,
      setStart,
      setResolve,
      setDone
      ))
    .then(() => delay(4000))
    .then(() => createDiv(
      "green",
      setLightRed,
      setLightYellow,
      setLightGreen,
      setStart,
      setResolve,
      setDone
      ))
    .then(() => delay(6000))
    .then(() => {
      setLightRed("");
      setLightGreen("");
      setLightYellow("");
      setDone("");
    })
    .then(console.log("Cleaned"))
    .catch(err => console.log(err));
};
